/* CONTAINER */
.contact-section {
  background-color: #f7f7f7;
  color: #333;
  padding: 40px 16px;
  text-align: center;
}

/* TITLE */
.menu-title {
  margin-bottom: 24px;
}

.menu-title h1 {
  font-size: 2em;
  margin: 0;
  color: #222;
}

.menu-title span {
  font-size: 14px;
  letter-spacing: 1.5px;
  color: #777;
  text-transform: uppercase;
}

/* INTRO TEXT */
.contact-text {
  font-size: 1em;
  line-height: 1.5;
  margin-bottom: 32px;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
}

/* CONTACT CARDS */
.contact-cards {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

/* CARD */
.contact-card {
  background-color: #fff;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
  transition:
    transform 0.2s ease,
    box-shadow 0.2s ease;
}

.contact-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12);
}

/* ICON */
.contact-icon {
  font-size: 36px;
  margin-bottom: 12px;
  color: #444;
}

/* TITLE */
.contact-title {
  font-size: 1.1em;
  font-weight: 600;
  margin-bottom: 6px;
}

/* LINES */
.contact-line {
  display: block;
  font-size: 0.95em;
  color: #666;
  margin-bottom: 4px;
  word-wrap: break-word;
}

/* TABLET */
@media (min-width: 480px) {
  .contact-cards {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }

  .contact-card {
    min-width: 180px;
    max-width: 220px;
    flex: 1 1 200px;
  }
}

/* DESKTOP */
@media (min-width: 768px) {
  .contact-section {
    padding: 60px 32px;
  }

  .menu-title h1 {
    font-size: 2.5em;
  }

  .menu-title span {
    font-size: 16px;
  }

  .contact-text {
    font-size: 1.05em;
  }

  .contact-icon {
    font-size: 40px;
  }

  .contact-title {
    font-size: 1.15em;
  }

  .contact-line {
    font-size: 1em;
  }
}
