/* RESET */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* OVERLAY */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  background-color: rgba(0, 0, 0, 0.4);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.4s ease;
  z-index: 999;
}

.overlay.open {
  opacity: 1;
  pointer-events: all;
}

/* HEADER */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #222;
  z-index: 1000;
  transition:
    box-shadow 0.3s ease,
    background 0.3s ease;
}

.header.scrolled {
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.35);
}

/* HEADER INNER */
.header-inner {
  height: 64px;
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* LOGO */
.logo {
  width: 72px;
  height: auto;
  flex-shrink: 0;
}

/* HAMBURGER */
.menu-toggle {
  position: relative;
  width: 30px;
  height: 30px; /* square za pravi X */
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 1100;
}

.menu-toggle span {
  position: absolute;
  width: 100%;
  height: 3px;
  background: #fff;
  border-radius: 2px;
  transition: all 0.4s ease;
}

/* Pozicioniranje linija pre animacije */
.menu-toggle span:nth-child(1) {
  top: 0;
}

.menu-toggle span:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

.menu-toggle span:nth-child(3) {
  bottom: 0;
}

/* Kada je meni otvoren â€“ pravi X */
.menu-toggle.open span:nth-child(1) {
  top: 50%;
  transform: rotate(45deg);
}

.menu-toggle.open span:nth-child(2) {
  opacity: 0;
}

.menu-toggle.open span:nth-child(3) {
  top: 50%;
  transform: rotate(-45deg);
}

/* NAV MOBILE (ispod headera) */
.nav {
  position: fixed;
  top: 64px;
  left: 0;
  width: 100%;
  height: calc(100% - 64px);
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  background-color: #f7f7f7;
  overflow-y: auto;

  transform: translateY(-100%);
  opacity: 0;
  transition:
    transform 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55),
    opacity 0.4s ease;

  z-index: 999;
}

.nav.open {
  transform: translateY(0);
  opacity: 1;
}

.nav li {
  padding: 18px 0;
  font-size: 24px;
  font-weight: 500;
  color: #222;
  cursor: pointer;
  transition:
    color 0.2s ease,
    transform 0.3s ease;
}

.nav li:hover {
  color: #f2a900;
  transform: scale(1.05);
}

/* TABLET / DESKTOP */
@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }

  .header-inner {
    padding: 0 32px;
  }

  .nav {
    position: static;
    flex-direction: row;
    transform: none;
    opacity: 1;
    height: auto;
    padding: 0;
    background: none;
    justify-content: flex-end;
  }

  .nav li {
    font-size: 16px;
    padding: 0 16px;
    color: #222;
  }

  .nav li:hover {
    color: #f2a900;
    transform: none;
  }
}
